$stackWrapWidth: 600vw;
$descriptionHeight: 64px;
$ease-in-out-cubic: cubic-bezier(0.645,0.045,0.355,1);
//
//.stack {
//  background: #0f0f0f;
//}

.stack__container {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  gap: 2rem;
  //background: #0f0f0f;
  padding-top: 10%;
}

.stack__item {
  padding: 3rem;
}

.stack__img {
  position: relative;
}

.stack__img img {
  opacity: .7;
  transition: all .5s  $ease-in-out-cubic;
  border-radius: 30px;
}

.stack__desc {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  //height: calc(100% - $descriptionHeight);
  margin-bottom: $descriptionHeight;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;

  overflow: hidden;

}

.stack__desc-wrap {
  transform: translateY($descriptionHeight - 8px);
  transition: transform .5s $ease-in-out-cubic;
}

.stack-labels__item {
  font-weight: 600;
  text-transform: uppercase;
  position: relative;
  color: #0f0;
}


.stack__link:hover .stack__desc-wrap {
  transform: translate(0);
}

.stack__link:hover .stack__img img {
  opacity: .4;
}


@media screen and (min-width: 600px) {
  .stack__container {
    flex-direction: row;
    height: 100vh;
    width: $stackWrapWidth;
    overflow: hidden;
    padding-top: 0;

    position: fixed;
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
  }

  .stack__item {
    width: 600px;
    height: 50%;
    padding: 0;
  }

  .stack__item:first-child {
    margin-left: -20rem;
  }
}