@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
    font-family: 'OCR-A_BT';
    src: url('./fonts/OCR-A_BT.ttf') format('ttf'), url('./fonts/OCR-A_BT.woff2') format('woff2');
}

body {
  margin: 0;
  font-family: 'OCR-A_BT', sans-serif;
  font-weight: 400;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.1;
    -webkit-transition: .45s ease-in-out;
    transition: .45s ease-in-out;

    background-color: #132301;
}