.projects__title-wrap {
  overflow: hidden;
  white-space: nowrap;
  letter-spacing: .3px;
  font-size: .75rem;
  position: fixed;
  z-index: 20;
  left: 50%;
  transform: translateX(-50%);
  //bottom: 5%;
  top: 2%;
  width: 20rem;
}

.projects__title {
  width: 20rem;

  font-family: sans-serif;
  font-weight: 100;
  color: white;
  border: solid 1px #fafafa;
  padding: .5rem 1rem;
  display: flex;
  border-radius: 30px;
  cursor: pointer;
  //transition: .3s ease-out;
}

.projects__title p {
  will-change: transform;
  transition: .3s ease-out;
}

.projects__title p:nth-child(1) {
  display: inline-block;
  -webkit-animation: marquee 15s linear infinite;
  animation: marquee 15s linear infinite;
}

.projects__title p:nth-child(2) {
  display: inline-block;
  -webkit-animation: marquee2 15s linear infinite;
  animation: marquee2 15s linear infinite;
  -webkit-animation-delay: 7.5s;
  animation-delay: 7.5s;
  //background-color: #0b0b0b;
}

.projects__title .dot {
  width: 8px;
  height: 8px;
  background-color: #0f0;
  display: inline-block;
  border-radius: 50px;
  position: relative;
  top: -1px;
  left: 4px;
}

@-webkit-keyframes marquee {
  0% {
    transform: translate(0);
  }

  to {
    transform: translate(-100%);
  }
}

@keyframes marquee {
  0% {
    transform: translate(0);
  }

  to {
    transform: translate(-100%);
  }
}

@-webkit-keyframes marquee2 {
  0% {
    transform: translate(-50%);
  }

  to {
    transform: translate(-150%);
  }
}

@keyframes marquee2 {
  0% {
    transform: translate(-50%);
  }

  to {
    transform: translate(-150%);
  }
}

@-webkit-keyframes marquee-mobile {
  0% {
    transform: translate(100%)
  }

  to {
    transform: translate(-100%)
  }
}

@keyframes marquee-mobile {
  0% {
    transform: translate(100%)
  }

  to {
    transform: translate(-100%)
  }
}

@-webkit-keyframes marquee2-mobile {
  0% {
    transform: translate(0)
  }

  to {
    transform: translate(-200%)
  }
}

@keyframes marquee2-mobile {
  0% {
    transform: translate(0)
  }

  to {
    transform: translate(-200%)
  }
}

@media screen and (min-width: 600px) {
  .projects__title-wrap {
    top: auto;
    bottom: 5%;
  }
}
